.popup__container {
  position: relative;
  max-width: 430px;
  background: #ffffff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 34px 36px 37px 36px;
  box-sizing: border-box;
}

@media screen and (max-width: 700px) {
  .popup__container {
     max-width: 282px;
     padding: 25px 22px;
  }
}
